import './wrapper.css';
import Inicio from './Inicio';

export default function(prop){
    
    const mostraRoot = (e)=>{
     let itemA = document.querySelectorAll(".item-a");
     let i = 0;
     while(i < itemA.length){
        if(i < itemA.length){
            itemA[i].className = "item-a";
            i++;
        }
        e.currentTarget.className = "item-a active";
    }
   }
    return (
        <div className="wrapper">
            <div className="view">
                <Inicio/>
            </div>
        </div>
    );
}