import AspaIcon  from '../img/aspa.png';
import { NavLink } from 'react-router-dom';
const FeedBack = () =>{

    return(
        <div className='feed'>
            <img src={AspaIcon} className='aspa'/>
            <h1>Se Creo La Cuenta Correctamente</h1>
            <p className='consejo'>Puedes ver la cuenta en :</p>
            <NavLink to='/Cuentas' className='redireccion'>Cuentas</NavLink>
        </div>
    )
}

export default FeedBack;