import React,{useState} from 'react';
import {NavLink} from 'react-router-dom'
import Avatar from './img/avatar.png';
import Subir from './img/subir.png';


const Nav = () =>{
    
    const admin = localStorage.getItem('admin') ? localStorage.getItem('admin') : localStorage.setItem('admin', 'userName');
    const [nameActulizar, cambiarNameActualizar] = useState(admin);
    const [urlStado, cambiarUrlStado] = useState('')
    const [userName, changeUserName] = useState(false)

    const mostraRoot = (e)=>{
        let itemA = document.querySelectorAll(".item-a");
        let i = 0;
        while(i < itemA.length){
           if(i < itemA.length){
               itemA[i].className = "item-a";
               i++;
           }
           e.currentTarget.className = "item-a active";
       }
      }
     const fileUpdate = (e) =>{
       const render = new FileReader();
       render.readAsDataURL(e.target.files[0])
       render.addEventListener('load',(e)=>{
        cambiarUrlStado(e.currentTarget.result)
        localStorage.setItem('UrlImgAvatar',e.currentTarget.result)
       })
     }

    return(
        <div className="sideBar">
        <div className="avatar">
          <div className='imgBx'>
             {
                 urlStado.length > 0 ? 
                 <img src={urlStado} style={{position:'absolute',top:'0',left:'0',width:'100%',height:'100%',objectFit:'cover'}} className='avatarUser'/>
                 : localStorage.getItem('UrlImgAvatar') ? 
                 <img src={localStorage.getItem('UrlImgAvatar')} style={{position:'absolute',top:'0',left:'0',width:'100%',height:'100%',objectFit:'cover'}} className='avatarUser'/>
                 : console.log('nada')
             }
          <img src={Avatar} alt="Eva Green" className='determinado'/>
          <div className='update-avatar'>
             <img src={Subir}/>
             <p>Subir Avatar</p>
             <input type='file' onChange={fileUpdate}/>
          </div>
          </div>
          <span className='box-username' >{
           userName ? 
           <form className='box-form-edit-username' 
           onSubmit={(e)=>{
               e.preventDefault();
               changeUserName(false)
               let i = document.querySelector('.ri-edit-box-line');
                 if(i.classList.contains('ocultar')){
                     i.classList.remove('ocultar')
                 }

           }}>
               <input 
               type='text'
               placeholder='Nuevo Nombre' 
               onChange={(e)=> {
                 cambiarNameActualizar(e.target.value)
                 localStorage.setItem('admin', e.target.value)
                } } 
               value={nameActulizar}/>
               <input type='submit' value='Actualizar' className=''/>
           </form>
            : nameActulizar.length > 0 ? <p>{nameActulizar}</p> 
            : <p>userName</p>
              }
             <i className="ri-edit-box-line" 
             onClick={(e)=> 
               {
                changeUserName(!userName) 
                e.currentTarget.classList.toggle('ocultar')
                }}/>
              </span>   
         </div>
         <ul className="sideBar-options">
             <NavLink to='/' onClick={mostraRoot} className="sideBar-options-item"><i className="ri-home-line"></i>Inicio</NavLink>
             <NavLink to='/CrearCuenta' onClick={mostraRoot} className="sideBar-options-item"><i className="ri-account-box-line"></i>Crear Cuenta</NavLink>
             <NavLink to='/Cuentas' onClick={mostraRoot} className="sideBar-options-item"><i className="ri-account-circle-fill"></i>Cuentas</NavLink>
             <NavLink to='/Gestion' onClick={mostraRoot} className="sideBar-options-item"><i className="ri-function-line"></i>Gestion</NavLink>
             <NavLink to='/Resultados' onClick={mostraRoot} className="sideBar-options-item"><i className="ri-slideshow-line"></i>Resultados</NavLink>
         </ul> 
       </div>
    )
}

export default Nav;