import Home from './Home';
import Create from './Create'
import Agregar  from './Agregar';
import Cuentas from './Cuentas';
import Resultados from './Resultados';
import { Router, Route, Routes} from 'react-router-dom';
import Nav from './Nav';

function App() {
  let nAccount  = localStorage.getItem("nameCuenta");
  let nClient  = localStorage.getItem("nameObject");
  return ( 
<div className='app'>
  <Nav/>
   <Routes>
    <Route  path="/"  element={<Home/>}/> 
     <Route path="/Resultados" element={<Resultados />}/> 
     <Route path="/Gestion" element={<Agregar nombreDeLaCuenta={nAccount} nombreDelCliente={nClient}/>}/> 
      <Route path="/CrearCuenta" element={<Create/>}/> 
      <Route path="/Cuentas" element={<Cuentas/>}/>
    </Routes> 
   </div>
  );
}

export default App;
